<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <div v-for="(group, index) of centerGroups">
        <form-card
          :group="group"
          v-model="form"
          @updateHandle="updateHandle"
        ></form-card>
        <div class="card" v-if="index == 0">
          <div class="card-header">
            <h6 class="title">Hình ảnh</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <my-image
                multiple="true"
                v-model="form.list_image"
                feautureImage=1
                @changeFeautureImage="changeFeautureImage"
              ></my-image>
            </div>
          </div>
        </div>
        <div class="card" v-if="index == 0 && !variants.length">
          <div class="card-header">
            <h6 class="title">Giá</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <div class="col-sm-12 col-lg-2 col-md-2">
                <b class="required">Giá</b>
              </div>
              <div class="col-sm-12 col-lg-10 col-md-10">
                <my-money
                  placeholder="Giá"
                  fixed=0
                  v-model="price"
                  :class="errors.has('price')?'border-danger':''"
                  v-validate="'required'"
                  data-vv-name="price"
                  data-vv-as="Giá"
                >
                  <template slot="append">VNĐ</template>
                </my-money>
                <span class="text-danger" v-if="errors.has('price')">{{ errors.first('price') }}</span>
              </div>
            </div>
            <div class="row">
              <div class="col-sm-12 col-lg-2 col-md-2">
                <b>Giá so sánh</b>
              </div>
              <div class="col-sm-12 col-lg-10 col-md-10">
                <my-money
                  placeholder="Giá so sánh"
                  fixed=0
                  v-model="priceCompare"
                >
                  <template slot="append">VNĐ</template>
                </my-money>
              </div>
            </div>
          </div>
        </div>
        <div class="card" v-if="index == 0 && variantAttributes.length">
          <div class="card-header">
            <h6 class="title">Phiên bản sản phẩm</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <div class="col-sm-12">
                <el-switch
                  v-model="stock_manage"
                  active-text="Quản lý tồn kho"
                >
                </el-switch>
              </div>
            </div>
            <div class="row" v-for="item of variantAttributes">
              <div class="col-sm-12 col-lg-2 col-md-2">
                <b>{{item.name}}</b>
              </div>
              <div class="col-sm-12 col-lg-10 col-md-10">
                <my-select
                  :attribute="item"
                  :placeholder="item.name"
                  v-model="attributes[item.name]"
                >
                </my-select>
              </div>
            </div>
            <div class="row">
              <div v-for="(item, index) of variants" class="col-md-12" style="background-color: aliceblue; margin-top: 10px">
                <div class="row">
                  <div class="col-md-6">
                    <el-switch
                      v-model="item.status"
                      active-value="active"
                      inactive-value="inactive"
                      active-text="Tạo phiên bản"
                    >
                    </el-switch>
                  </div>
                  <div class="col-md-6">
                    <span style="width: 100%;">Tên thuộc tính: {{item.attributeTitle}}</span>
                  </div>
                </div>
                <div class="row clearfix">
                  <div class="col-md-6">
                    <span>Tên phiên bản</span>
                    <el-input
                      placeholder="Tên phiên bản"
                      v-model="item.title"
                      :class="errors.has('title_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'title_variant_' + index"
                      data-vv-as="Tên phiên bản"
                    ></el-input>
                    <span class="text-danger" v-if="errors.has('title_variant_' + index)">{{ errors.first('title_variant_' + index) }}</span>
                  </div>
                  <div class="col-sm-6" v-if="stock_manage">
                    <span>Tồn kho:</span>
                    <el-input
                      placeholder="Tồn kho"
                      v-model="item.stock_quant"
                      :class="errors.has('stock_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'stock_variant_' + index"
                      data-vv-as="Tồn kho"
                    ></el-input>
                    <span class="text-danger" v-if="errors.has('stock_variant_' + index)">{{ errors.first('stock_variant_' + index) }}</span>
                  </div>
                </div>
                <div class="row">
                  <div class="col-sm-6">
                    <span>Giá bán:</span>
                    <my-money
                      placeholder="Giá bán"
                      fixed=0
                      v-model="item.price"
                      :class="errors.has('price_variant_' + index)?'border-danger':''"
                      v-validate="'required'"
                      :data-vv-name="'price_variant_' + index"
                      data-vv-as="Giá"
                    >
                      <template slot="append">VNĐ</template>
                    </my-money>
                    <span class="text-danger" v-if="errors.has('price_variant_' + index)">{{ errors.first('price_variant_' + index) }}</span>
                  </div>
                  <div class="col-sm-6">
                    <span>Giá so sánh:</span>
                    <my-money
                      placeholder="Giá so sánh"
                      fixed=0
                      v-model="item.price_compare"
                    >
                      <template slot="append">VNĐ</template>
                    </my-money>
                  </div>
                </div>
                <div class="row">
                  <div class="col-md-12">
                    <span>Hình ảnh</span>
                    <div>
                      <my-image
                        multiple="true"
                        v-model="item.list_image"
                      ></my-image>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card" v-if="index == 0 && productAttributes.length">
          <div class="card-header">
            <h6 class="title">Thuộc tính sản phẩm</h6>
          </div>
          <div class="card-body form-card">
            <div class="row" v-for="item of productAttributes">
              <div class="col-sm-12 col-lg-2 col-md-2">
                <b>{{item.name}}</b>
              </div>
              <div class="col-sm-12 col-lg-10 col-md-10">
                <my-select
                  :attribute="item"
                  :placeholder="item.name"
                  v-model="productAttribute[item.name]"
                >
                </my-select>
              </div>
            </div>
          </div>
        </div>
      </div>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <div v-for="group of sideGroups">
        <form-card
          hasTop="true"
          :group="group"
          v-model="form"
        >
        </form-card>
        <div class="card" v-if="listView">
          <div class="card-header">
            <h6 class="title">Giao diện</h6>
          </div>
          <div class="card-body form-card">
            <div class="row">
              <div class="col-sm-12">
                <my-select
                  class="full-width"
                  :attribute="listView"
                  placeholder="Giao diện"
                  v-model="form.template"
                >
                </my-select>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Lưu lại</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import { Message, Input, Button, Switch } from 'element-ui';
import MySelect from 'src/components/UIComponents/Select'
import MyImage from 'src/components/UIComponents/Image'
import MyMoney from 'src/components/UIComponents/Money'
import dataFrom from './product-form'
import { mapState } from 'vuex'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElSwitch: Switch,
    ElButton: Button,
    MySelect,
    MyMoney,
    MyImage,
    Message,
    CustomField
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    _form['template'] = '';
    this.$store.dispatch('getCustomField', {type: 'product'});
    this.$store.dispatch('getListThemeView', 'product');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      form: _form,
      stock_manage: true,
      attributes: {},
      productAttribute: {},
      variants: {},
      priceCompare: '',
      price: '',
      custom_field: _custom_field,
      show_fields: []
    }
  },

  computed: {
    ...mapState(['productAttributes', 'variantAttributes']),
    customField: {
      get() {
        return this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mặc định',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    }
  },

  mounted() {
    this.$store.dispatch('setPageTitle', 'Tạo sản phẩm');
    this.$store.dispatch('fetchProductAttributes');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Lưu lại',
      type: 'primary',
      icon: '',
      callback: this.save
    }]);
    this.getContext();
    this.form.status = 'active';
    this.form.stop_selling = 'publish';
    this.form['seo.meta_robots'] = 'index, follow';
  },

  methods: {
    listVariantTitle() {
      let self = this;
      let result = [];
      let attributes = Object.values(self.attributes);
      if (attributes) {
        result = attributes.reduce(function (a, b) {
          return a.reduce(function (r, v) {
            return r.concat(b.map(function (w) {
              return [].concat(v, w);
            }));
          }, []);
        });
      }
      return result;
    },
    cancel() {
      this.$router.push({name: 'AllProducts'});
    },
    changeFeautureImage(value){
      this.form.image = value;
    },
    updateHandle(){
      let self = this;
      self.form.handle = self.form.title;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.form.handle = result;
      });
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          let collections = [];
          if (self.form.objCollection) {
            for (var item in self.form.objCollection) {
              let collection = self.form.objCollection[item];
              collections.push(collection.id);
            }
          }
          self.form.collections = collections;
          if (!self.variants.length) {
            let variant = {}
            variant.title = 'Default';
            variant.options = '';
            variant.price = self.price;
            variant.price_compare = self.priceCompare;
            variant.stock_quant = 1;
            variant.status = 'active';
            variant.list_image = '';
            self.form.variants = {variant};
          } else self.form.variants = self.variants;
          self.form.productAttribute = self.productAttribute;
          if (!self.image){
            self.form.image = self.form.list_image?self.form.list_image[0].name:'';
          }
          self.$store.dispatch('storeProduct', self.form).then(res => {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'product', res.id));

            Message({
              message: 'Tạo mới thành công',
              type: 'success'
            });
            self.$router.push({name: 'AllProducts'})
          }, res => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      })
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'products'});
      this.$store.state.ctx = ctx;
      return ctx;
    }
  },
  watch: {
    'form.title'(newVal, oldVal) {
      let self = this;
      this.form['seo.meta_title'] = newVal;
      let data = {
        handle: self.$util.createHandle(newVal),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.form.handle = result;
      });
    },
    'form.description'(newVal, oldVal) {
      this.form['seo.meta_description'] = newVal;
    },
    productAttributes(newVal, oldVal){
      this.productAttributes.forEach(function (productAttribute) {
        productAttribute.multiple = true;
        productAttribute.child.forEach(function (item) {
          item.title = item.name;
          item.value = item.name;
        })
        productAttribute.options = productAttribute.child;
      })
    },
    variantAttributes(newVal, oldVal){
      this.variantAttributes.forEach(function (variantAttribute) {
        variantAttribute.multiple = true;
        variantAttribute.child.forEach(function (item) {
          item.title = item.name;
          item.value = item.name;
        })
        variantAttribute.options = variantAttribute.child;
      })
    },
    attributes:{
      handler(newVal, oldVal){
        let listVariantTitle = this.listVariantTitle();
        this.variants = listVariantTitle.map(function (item) {
          let title = item.join('/');
          return {
            title: title,
            options: item,
            attributeTitle: title,
            status: 'active'
          }
        });
      },
     deep: true
    },
    'form': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.productAttributes = [];
    this.$store.state.variantAttributes = [];
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
